<template>
  <div>
    <div class="title">
      关于我
    </div>
    <main>
      <ul class="info-list">
        <li>
          <div>
            <img src="/asset/img/i_age.svg" alt="">
          </div>
          <p>年龄/{{ info.age }}</p>
        </li>
        <li>
          <div>
            <img src="/asset/img/i_edu.svg" alt="">
          </div>
          <p>学历/本科</p>
        </li>
        <li>
          <div>
            <img src="/asset/img/i_site.svg" alt="">
          </div>
          <p>坐标/{{ info.position }}</p>
        </li>
        <li>
          <div>
            <img src="/asset/img/i_status.svg" alt="">
          </div>
          <p>状态/{{ info.status }}</p>
        </li>
      </ul>
      <p>一年web前端开发经验</p>
      <p>熟悉MV*开发,深谙自动化,模块化开发之道</p>
      <p>高效的自学能力,具备独立分析解决问题能力</p>
      <p>强烈的自我驱动力,代码强迫症患者</p>
    </main>
  </div>
</template>

<script>
  export default{
    data(){
      return {
        info: {
          age: null,
          position: null,
          status: null
        }
      }
    },
    created(){
      this.axios.get('/asset/demo.json')
        .then(({data}) => this.info = data.info);
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  @keyframes effect1 {
    0% {
      opacity: .1;
      transform: scale(1);
    }
    100% {
      transform: scale(1.3);
      opacity: 0;
    }
  }

  @keyframes effect2 {
    from {
      opacity: 1;
      transform: scale(1);
    }
    to {
      transform: scale(1.6);
      opacity: 0;
    }
  }

  .info-list {
    width: 800px;
    display: flex;
    justify-content: space-around;
    margin: 50px auto;
    li {
      width: 100px;
      text-align: center;
      line-height: 3;
      font-size: 16px;
      div {
        width: 90px;
        height: 90px;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, .1);
        position: relative;
        cursor: pointer;
        transition: 1.3s;
        &:hover {
          transform: scale(.9);
        }
        &:hover {
          &:before {
            animation: effect1 1.3s ease-out 75ms;
          }
          &:after {
            animation: effect1 1.3s ease-out;
          }
          transform: scale(.9);
        }
        &:before {
          content: '';
          display: block;
          width: 100%;
          height: 100%;
          position: absolute;
          border: 6px solid #fff;
          top: -3px;
          left: -3px;
          border-radius: 50%;
          opacity: 0;
        }
        &:after {
          content: '';
          display: block;
          width: 100%;
          height: 100%;
          position: absolute;
          top: -1px;
          left: -1px;
          border: 2px solid #fff;
          border-radius: 50%;
          opacity: 0;
        }
        img {
          width: 50%;
          position: absolute;
          left: 25%;
          top: 25%;
        }
      }
    }
  }

  main {
    > p {
      font-size: 16px;
      text-align: center;
      line-height: 2.5;
    }
  }
</style>



