<template>
  <div id="app" class="swiper-container">
    <header>
      <img src="/asset/img/head-photo.jpg">
    </header>
    <div class="swiper-wrapper">
      <v-home class="swiper-slide  app-silde"></v-home>
      <v-about class="swiper-slide  app-silde"></v-about>
      <v-skill class="swiper-slide app-silde"></v-skill>
      <v-experience class="swiper-slide app-silde"></v-experience>
      <v-demo class="swiper-slide app-silde"></v-demo>
      <v-contact class="swiper-slide app-silde"></v-contact>
    </div>

    <!-- 分页器 -->
    <div id="pages">
      <ul>
        <li v-for="(item,index) in pages" @click="slideTo(index)" @mouseover="active(index,true)"
            @mouseout="unActive(index,false)">
          <div :class="{active:isActive[index]}">
            <img :src="item.bg">
          </div>
          <span :class="{active:textActive[index]}">{{ item.text }}</span>
        </li>
      </ul>
    </div>
    <!-- 分页器 end -->
    <div class="arrow" v-if="notLast"></div>
  </div>

</template>

<script>
  import vHome from '@/components/home';
  import vAbout from '@/components/about';
  import vSkill from '@/components/skill';
  import vExperience from '@/components/experience';
  import vDemo from '@/components/demo';
  import vContact from '@/components/contact';

  export default {
    name: 'app',
    data(){
      return {
        mySwiper: null,
        notLast:true,
        pages: [
          {text: '首页', bg: '/asset/img/d_home.svg'},
          {text: '关于我', bg: '/asset/img/d_info.svg'},
          {text: '技能栈', bg: '/asset/img/d_skills.svg'},
          {text: '经历', bg: '/asset/img/d_exp.svg'},
          {text: '作品集', bg: '/asset/img/d_works.svg'},
          {text: '联系我', bg: '/asset/img/d_contact.svg'}
        ],
        isActive: [true, false, false, false, false, false],
        isClick: [true, false, false, false, false, false],
        textActive: [false, false, false, false, false, false]
      }
    },
    methods: {
      slideTo(index){
        let _isClick = this.isClick;

        _isClick = _isClick.map(() => false);
        _isClick[index] = true;
        this.isClick = null;
        this.isClick = _isClick;
        //跳转页面
        this.mySwiper.slideTo(index, 500, true);
      },
      active(index, onOff){ //处理激活到某一个页面的函数
        let _isActive = this.isActive,
          _textActive = this.textActive;

        _isActive[index] = _textActive[index] = onOff;
        this.isActive = this.textActive = null;
        this.isActive = _isActive;
        this.textActive = _textActive;

      },
      unActive(index){
        if (!this.isClick[index]) {
          let _isActive = this.isActive,
            _textActive = this.textActive;

          _textActive = _textActive.map(() => false);

          _isActive[index] = false;
          this.isActive = null;
          this.isActive = _isActive;
          this.textActive = null;
          this.textActive = _textActive;
        }
      }
    },
    mounted(){
      let self = this;

      this.mySwiper = new this.Swiper('#app', {
        direction: 'vertical',
        speed: 500,
        mousewheelControl: true,
        simulateTouch: false,
        onSlideChangeEnd: function (swiper) {
          let index = swiper.activeIndex,
            _isActive = self.isActive;

          _isActive = _isActive.map(() => false);
          _isActive[index] = true;
          self.isActive = null;
          self.isActive = _isActive;
          self.slideTo(index);
          //是否渲染arrow
          self.notLast = index ===5 ? false : true;

        }
      })
    },
    components: {
      vHome,
      vAbout,
      vDemo,
      vSkill,
      vExperience,
      vContact
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "./common/reset";
  @import "./common/swiper.css";

  .title {
    width: 94%;
    font-size: 24px;
    text-align: center;
    margin-top: 70px;
    text-indent: 3em;
  }

  header {
    width: 100vw;
    height: 60px;
    position: fixed;
    z-index: 1000;
    top: 0;
    padding: 10px 3% 0;
    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
    }
  }

  #app {
    height: 100vh;
    overflow: hidden;
  }

  #pages {
    height: 192px;
    margin-top: -96px;
    position: fixed;
    top: 50%;
    right: 20px;
    z-index: 1000;
    li {
      width: 32px;
      height: 32px;
      position: relative;
      border: 1px solid transparent;
      cursor: pointer;
      div {
        width: 8px;
        height: 8px;
        border-top: 1px solid transparent;
        background: rgba(0, 0, 0, .6);
        background-size: cover;
        border-radius: 50%;
        margin: 11px auto 0;
        transition: .3s;
        transform-origin: 50% 50%;
        &.active {
          transform: scale(4);
        }
        img {
          width: 6px;
          height: 6px;
          margin: auto;
        }
      }
      span {
        width: 50px;
        line-height: 20px;
        text-align: center;
        position: absolute;
        top: 5px;
        transform: translateX(-170%);
        transition: .3s;
        background-color: rgba(0, 0, 0, .6);
        opacity: 0;
        padding:5px ;
        border-radius: 6px;
        &::after {
          content: "";
          position: absolute;
          top: 5px;
          right: -12px;
          border-left: 12px solid rgba(0, 0, 0, .6);
          border-top: 10px solid transparent;
          border-bottom: 10px solid transparent;
        }
        &.active {
          opacity: 1;
        }
      }
    }
  }

  .app-silde {
    width: 100vw;
    height: 100vh;
    padding: 0 3%;
    position: relative;
    @mixin bg($color) {
      background: url(/asset/img/bg.png) $color;
    }
    &:nth-of-type(1) {
      @include bg(#87b0a5);
    }
    &:nth-of-type(2) {
      @include bg(#109085);
    }
    &:nth-of-type(3) {
      @include bg(#4d5e8f);
    }
    &:nth-of-type(4) {
      @include bg(#945c4c);
    }
    &:nth-of-type(5) {
      @include bg(#4b85a0);
    }
    &:nth-of-type(6) {
      @include bg(#a29971);
    }
  }
  @keyframes move {
    from{
      opacity:.3;
      transform: translateY(-50%);
    }
    to{
      opacity:1;
      transform: translateY(50%);
    }
  }
  .arrow {
    position: fixed;
    bottom: 30px;
    height: 20px;
    width: 100%;
    background: url(/asset/img/arrow.svg) no-repeat center;
    background-size: 20px 100%;
    z-index: 10000;
    margin: auto;
    animation: move 2s linear infinite alternate;
  }
</style>



