<template>
  <div>
    <div class="title">
      经历
    </div>
    <main>
      <div class="swiper-container experience">
        <div class="swiper-wrapper">
          <div class="swiper-slide content clear" v-for="(item,index) in experience" :key="index">
            <div class="left pull-left"
                 :style="{ background: `url(${item.img}) no-repeat center center`,backgroundSize:'50%'}"></div>
            <div class="right pull-left">
              <h3>{{item.company}}</h3>
              <p>{{item.duration}}</p>
              <p>{{item.post}}</p>
              <p>{{item.description}}</p>
              <ul>
                <li v-for="(i,index) in item.details">{{i}}</li>
              </ul>
            </div>
          </div>
        </div>
        <div class="swiper-button-prev"></div>
        <div class="swiper-button-next"></div>
      </div>
      <p>鬼知道这些年来我经历了什么</p>
    </main>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        swiper: null,
        experience: []
      }
    },
    methods: {
      initSwiper(){
        let mySwiper = new Swiper('.experience', {
          effect: 'flip',
          prevButton: '.swiper-button-prev',
          nextButton: '.swiper-button-next'
        })
      }
    },
    mounted(){
      this.axios.get('/asset/demo.json').then(({data}) => {
        this.experience = data.experience;
        setTimeout(() => {
          this.initSwiper();
        }, 0)
      });
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  main {
    .swiper-button-prev {
      background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20viewBox%3D'0%200%2027%2044'%3E%3Cpath%20d%3D'M0%2C22L22%2C0l2.1%2C2.1L4.2%2C22l19.9%2C19.9L22%2C44L0%2C22L0%2C22L0%2C22z'%20fill%3D'%23945c4c'%2F%3E%3C%2Fsvg%3E")
    }
    .swiper-button-next {
      background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20viewBox%3D'0%200%2027%2044'%3E%3Cpath%20d%3D'M27%2C22L27%2C22L5%2C44l-2.1-2.1L22.8%2C22L2.9%2C2.1L5%2C0L27%2C22L27%2C22z'%20fill%3D'%23945c4c'%2F%3E%3C%2Fsvg%3E")
    }
    > p {
      text-align: center;
      font-size: 24px;
    }
    transform-style: preserve-3d;
    perspective: 2000px;
  }

  @keyframes rota {
    from {
      opacity: .8;
    }
    to {
      opacity: 1;
    }
  }

  .experience {
    width: 750px;
  }

  .content {
    width: 750px;
    height: 350px;
    background-color: #F2F4F5;
    border-radius: 10px;
    margin: 50px auto;
    box-shadow: 0 0 10px rgba(0, 0, 0, .6);
    animation: rota 2s linear infinite alternate;
    .left {
      width: 160px;
      height: 100%;
    }
    .right {
      width: 590px;
      height: 100%;
      padding-left: 50px;
      h3 {
        font-size: 20px;
        color: #945C4C;
        margin: 20px 0;
      }
      p {
        color: #AF7464;
        font-size: 14px;
        padding: 5px 0;
      }
      li {
        color: #000;
        position: relative;
        font-size: 14px;
        text-indent: .6em;
        line-height: 2;
        &:before {
          content: '';
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background-color: #AF7464;
          display: block;
          position: absolute;
          left: 0;
          top: 11px;
        }
      }
    }
  }
</style>



