<template>
  <div>
    <div class="title">
      作品集
    </div>
    <main>
      <div class="demos">
        <div v-for="item in demo">
          <h3 class="name"><a :href="item.src">{{ item.name }}</a></h3>
          <p>{{ item.description }}</p>
          <a :href="item.src" class="link"><img src="/asset/img/w_link.svg"></a>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
  export default{
    data(){
      return {
        demo: [
          {name: null, src: null, description: null}
        ]
      }
    },
    created(){
      this.axios.get('/asset/demo.json')
        .then(({data}) => this.demo = data.demo);
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .demos {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 10px;
    > div {
      width: 45%;
      height: 190px;
      box-shadow: 0 0 10px rgba(0, 0, 0, .6);
      background-color: #fff;
      border-radius: 4px;
      margin: 10px;
      padding: 10px;
      position: relative;
      > a {
        width: 200px;
        height: 30px;
        border: 1px solid #e6e6e6;
        display: block;
        position: absolute;
        bottom: 10px;
        border-radius: 4px;
        padding-top: 6px;
        &:hover {
          background: #e6e6e6;
        }
        img {
          width: 18px;
          height: 18px;
          margin: auto;
        }
      }
    }
    h3 {
      > a {
        color: #4985A0;
        font-size: 18px;
        margin: 5px 0;
        text-decoration: none;
        display: block;
      }
    }
    p {
      color: #666;
      font-size:14px;
    }

  }
</style>



